[class^="icon-"],
[class*=" icon-"] {
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-arrow:before {
  content: "\e900";
}

body {
  position: relative;
  height: 100%;
  margin: 0;
  padding: 0;
  color: #000;
  font-family: 'ProximaNova', sans-serif;
  font-weight: 400;
  line-height: 1;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  box-sizing: border-box;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: none;
}

table {
  border-collapse: collapse;
  border-width: 0;
  padding: 0;
  margin: 0;
}

ol,
ul {
  list-style-type: none;
}

html {
  height: 100%;
  -webkit-text-size-adjust: none;
  -ms-text-size-adjust: none;
}

img {
  display: block;
  max-width: 100%;
}

input,
textarea {
  border: none;
  outline: none;
  color: #000;
  font-family: 'ProximaNova', sans-serif;
  font-weight: 400;
  border-radius: 0;
  -webkit-appearance: none;
}

button {
  font-family: 'ProximaNova', sans-serif;
}

textarea {
  resize: none;
  overflow: auto;
}

a {
  text-decoration: none;
  transition: all 0.3s ease-out;
}

/* Slider */

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  user-select: none;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
  outline: none;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
  cursor: grab;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
}

.slick-arrow.slick-hidden {
  display: none;
}

.slick-arrow {
  position: absolute;
  z-index: 10;
  bottom: 33px;
  width: 50px;
  height: 50px;
  border: none;
  outline: none;
  padding: 0;
  color: #ffffff;
  background-color: transparent;
  font-size: 32px;
  cursor: pointer;
  transition: $transition;
}

.slick-arrow i {
  display: inline-block;
  text-shadow: 0 -3px 1px #000000, 0 -3px 1px #000000, 0 3px 1px #000000, 0 3px 1px #000000, -3px 0 1px #000000, 3px 0 1px #000000, -3px 0 1px #000000, 3px 0 1px #000000, -1px -3px 1px #000000, 1px -3px 1px #000000, -1px 3px 1px #000000, 1px 3px 1px #000000, -3px -1px 1px #000000, 3px -1px 1px #000000, -3px 1px 1px #000000, 3px 1px 1px #000000, -2px -3px 1px #000000, 2px -3px 1px #000000, -2px 3px 1px #000000, 2px 3px 1px #000000, -3px -2px 1px #000000, 3px -2px 1px #000000, -3px 2px 1px #000000, 3px 2px 1px #000000, -3px -3px 1px #000000, 3px -3px 1px #000000, -3px 3px 1px #000000, 3px 3px 1px #000000, -3px -3px 1px #000000, 3px -3px 1px #000000, -3px 3px 1px #000000, 3px 3px 1px #000000;
}

.slick-arrow:hover {
  color: #ff6666;
}

.slick-arrow:active {
  transform: translateY(4px);
}

.slick-arrow.slick-prev {
  left: -12px;
}

.slick-arrow.slick-prev i {
  display: block;
  transform: rotate(180deg);
}

.slick-arrow.slick-next {
  left: 196px;
  bottom: 30px;
}

.slick-dots {
  position: absolute;
  left: 117px;
  bottom: 51px;
  display: flex;
  transform: translateX(-50%);
}

.slick-dots li {
  width: 16px;
  height: 16px;
  box-sizing: border-box;
  border-radius: 50%;
  border: 2px solid #fff;
  padding: 2px;
  margin: 0 7.5px;
}

.slick-dots button {
  display: block;
  width: 100%;
  height: 100%;
  padding: 0;
  border: none;
  border-radius: 50%;
  background-color: transparent;
  text-indent: -9999px;
  transition: all 0.3s ease-out;
  cursor: pointer;
}

.slick-dots .slick-active button {
  background-color: #fff;
}

.btn {
  transition: all 0.3s ease-out;
  text-align: center;
}

.btn::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: -1;
  display: block;
  width: 100%;
  height: 100%;
  background-color: #fff;
  opacity: 0;
  transition: all 0.3s ease-out;
}

.btn:hover {
  cursor: pointer;
}

.btn:hover::after {
  bottom: 0;
  opacity: 1;
}

.btn-callback {
  position: relative;
  z-index: 1;
  overflow: hidden;
  display: inline-block;
  height: 49px;
  max-width: 100%;
  padding: 0 17px;
  font-size: 23px;
  font-weight: 700;
  line-height: 1.25;
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  background-image: linear-gradient(to top, #ff4f58 0%, #fd7048 100%);
  border: 2px solid #fff;
  border-radius: 4px;
  transition: 0.4s ease-out;
  cursor: pointer;
}

.btn-callback:hover {
  color: #ff6666;
  border-color: #ff6666;
}

.hidden {
  display: none;
}

.container {
  width: 1170px;
  padding: 0 15px;
  margin: auto;
  box-sizing: border-box;
}

.content {
  position: relative;
  padding-top: 89px;
  overflow: hidden;
}

.slider-background {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.slider-background__item {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: 50% 87%;
  background-size: cover;
}

.slider-background .slick-list,
.slider-background .slick-track {
  height: 100%;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: auto;
  min-width: 320px;
}

.header-top {
  padding: 9px 0;
  background-color: #fff;
}

.header-top__content {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.header__menu-btn {
  display: none;
}

.header-logo {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.header-logo__title {
  margin-right: 20px;
  color: #040404;
  font-size: 30px;
  line-height: 1.25;
  letter-spacing: 10px;
  text-transform: uppercase;
}

.header-logo__desc {
  font-size: 13px;
  font-weight: 400;
  line-height: 1.3;
  letter-spacing: 0.1px;
  text-transform: uppercase;
}

.header-contacts {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.header-contacts__number {
  margin-right: 17px;
  color: #000;
  font-size: 35px;
  font-weight: 700;
  line-height: 1.1;
  letter-spacing: .5px;
}

.header-contacts__number:hover {
  color: #ff6666;
}

.header-contacts .btn-callback {
  height: 32px;
  padding: 0 9px;
  font-size: 13px;
  text-transform: none;
  border-width: 1px;
}

.header-bottom {
  position: relative;
  z-index: 3;
  padding: 8px 0;
  background-color: rgba(0, 0, 0, 0.6);
}

.header-bottom .header-contacts {
  display: none;
}

.navigation-list {
  display: flex;
  flex-wrap: wrap;
  padding-left: 16px;
}

.navigation-list li {
  position: relative;
  flex: 1;
  text-align: center;
}

.navigation-list li:first-child {
  padding-left: 25px;
}

.navigation-list li:not(:last-child)::before {
  content: '';
  position: absolute;
  display: block;
  bottom: 0;
  right: 0;
  width: 1px;
  height: 100%;
  background-color: #ccc;
}

.navigation-list a {
  position: relative;
  color: #fff;
  font-size: 14px;
  line-height: 1.2;
  letter-spacing: 0.7px;
  text-transform: uppercase;
}

.navigation-list a:hover {
  color: #ff6666;
}

.advantages {
  position: relative;
  z-index: 5;
  margin-bottom: 5px;
  padding: 20px 0 10px;
  background-color: rgba(255, 255, 255, 0.8);
}

.advantages-list {
  overflow: auto;
}

.advantages-list__item {
  position: relative;
  float: left;
  width: 25%;
  padding: 0 10px;
  box-sizing: border-box;
  text-align: center;
}

.advantages-list__item p {
  padding: 8px 0 10px;
  line-height: 1.2;
}

.advantages-list__item h4 {
  font-size: 22px;
}

.advantages-list__image img {
  margin: 0 auto 18px;
}

.main-slider {
  position: relative;
  z-index: 2;
  width: 100%;
}

.main-slider::before {
  content: "";
  position: absolute;
  top: 0;
  left: -270%;
  display: block;
  width: 300%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  transform: skewX(-17deg);
}

.main-slider h2,
.main-slider h3 {
  font-family: 'Intro', sans-serif;
}

.main-slider h2::after,
.main-slider h3::after {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.7);
  transform: skewX(-17deg);
}

.main-slider h2 {
  position: relative;
  display: inline-block;
  padding: 11px 17px 8px 20px;
  margin-bottom: 10px;
  font-size: 90px;
  letter-spacing: -1.8px;
  text-transform: uppercase;
}

.main-slider h2::after {
  left: 5px;
}

.main-slider h3 {
  position: relative;
  display: inline-block;
  margin: 0 9px 0 -20px;
  padding: 3px 23px 17px;
  font-size: 36px;
  line-height: 1.1;
}

.main-slider h3::after {
  left: -1px;
}

.main-slider h3 span {
  padding: 0 34px;
  border-bottom: 3px solid;
}

.main-slider__item-wrap {
  position: relative;
  z-index: 3;
  padding: 38px 0 126px;
}

.main-slider__desc {
  max-width: 300px;
  margin: 24px 0 20px;
  color: #fff;
  font-size: 19px;
  line-height: 1.3;
  font-weight: 700;
  text-transform: uppercase;
}

.main-slider__price {
  position: relative;
  padding: 0 15px;
  color: #fff;
  font-family: 'Intro', sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
}

.main-slider__sub-info {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.main-slider strong {
  padding: 0 10px;
  font-size: 45px;
  line-height: 1.2;
}

.main-slider small {
  font-size: 12px;
}

.main-slider b {
  position: relative;
  padding-right: 10px;
  line-height: 1;
  font-size: 18px;
}

.main-slider b::after {
  content: "";
  position: absolute;
  top: 25%;
  right: 3px;
  z-index: 1;
  display: block;
  width: 3px;
  height: 60%;
  background-color: #fff;
  transform: skewX(-24deg);
}

.main-slider__price::before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to right, #2aeb2a 0%, #007500 100%);
  transform: skewX(-17deg);
  box-shadow: 3px 4px 5px rgba(0, 0, 0, 0.75);
}

.main-slider__btn a {
  position: relative;
  z-index: 1;
  overflow: hidden;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 49px;
  max-width: 100%;
  border: 2px solid #fff;
  border-radius: 4px;
  padding: 0 17px;
  color: #fff;
  background-image: linear-gradient(to top, #ff4f58 0%, #fd7048 100%);
  font-size: 23px;
  font-weight: 700;
  line-height: 1.25;
  text-transform: uppercase;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease-out;
}

.main-slider__btn a:hover {
  color: #ff6666;
}

.main-slider__info {
  position: relative;
}

.main-slider__info::before {
  content: "";
  position: absolute;
  z-index: 1;
  top: 0;
  left: -16px;
  display: block;
  width: 3px;
  height: 100%;
  background-color: #fff;
  transform: skewX(-16deg);
}

.main-slider .slick-list {
  overflow: visible;
}

.slider-background .slick-arrow {
  display: none!important;
}

@media screen and (max-width: 1355px) {
  .main-slider__price strong {
    font-size: 36px;
    line-height: 1.2;
  }
}

@media screen and (max-width: 1280px) {
  .slick-arrow.slick-prev {
    left: 32px;
  }

  .slick-dots {
    left: 157px;
  }

  .slick-arrow.slick-next {
    left: 236px;
  }

  .main-slider__item-wrap {
    margin-left: 40px;
  }
}

@media screen and (max-width: 1200px) {
  .container {
    width: 991px;
  }

  .main-slider h1,
  .main-slider h2 {
    font-size: 70px;
  }

  .main-slider h3 {
    font-size: 32px;
  }

  .main-slider h4 {
    font-size: 19px;
  }
}

@media (max-width: 992px) {
  .slick-arrow {
    font-size: 26px;
  }

  .slick-arrow.slick-prev {
    left: -12px;
  }

  .slick-dots {
    left: 115px;
  }

  .slick-arrow.slick-next {
    left: 196px;
  }
}

@media screen and (max-width: 992px) {
  .container {
    width: 768px;
  }

  .main-slider__item-wrap {
    margin-left: 0;
  }
}

@media screen and (max-width: 991px) {
  .main-slider-wrap::after {
    top: 0;
    height: 100%;
  }

  .main-slider-wrap h1,
  .main-slider-wrap h2 {
    font-size: 56px;
  }

  .main-slider-wrap h3 {
    font-size: 28px;
  }

  .btn {
    font-size: 16px;
  }

  .main-slider__price strong {
    font-size: 32px;
  }

  .advantages-list {
    -webkit-flex: 1 1 auto;
  }

  .advantages-list__item {
    width: 50%;
    margin-bottom: 20px;
  }

  .header-bottom {
    position: fixed;
    z-index: 20;
    top: 0;
    left: 0;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 20px;
    background-color: rgba(0, 0, 0, 0.9);
    transform: translate3d(-140%, 0, 0) scale(1.15);
    transition: all .5s;
  }

  .header-bottom.open {
    transform: translate3d(0, 0, 0) scale(1);
  }

  .header-bottom .container {
    width: 100%;
    margin: 0;
  }

  .header-bottom .header-contacts {
    position: relative;
    display: inline-block;
    padding-top: 40px;
  }

  .header-bottom .header-contacts::before {
    content: "";
    position: absolute;
    top: 20px;
    left: 0;
    right: 0;
    width: 100%;
    height: 2px;
    background-color: #fff;
  }

  .header-bottom .header-contacts a {
    display: block;
    width: 100%;
    margin-bottom: 20px;
    color: #fff;
  }

  .header-bottom .header-contacts .btn-callback {
    height: 50px;
    width: 100%;
    font-size: 16px;
  }

  .navigation-list {
    display: inline-flex;
    flex-direction: column;
    padding: 0;
  }

  .navigation-list li {
    display: inline-block;
    width: 100%;
    flex: inherit;
    padding: 10px 0;
    text-align: left;
  }

  .navigation-list li:first-child {
    padding-left: 0;
  }

  .navigation-list li:last-child::before {
    content: '';
    position: absolute;
    left: -10px;
    top: 50%;
    display: block;
    width: 2px;
    height: 75%;
    transform: translateY(-50%);
  }

  .navigation-list li:last-child:hover::before {
    background-color: #ff6666;
  }

  .navigation-list li:not(:last-child)::before {
    left: -10px;
    top: 50%;
    height: 75%;
    width: 2px;
    background-color: transparent;
    transform: translateY(-50%);
    transition: all 0.3s ease-out;
  }

  .navigation-list li.active::before,
  .navigation-list li:hover::before {
    background-color: #ff6666;
  }

  .navigation-list li.active a {
    color: #ff6666;
  }

  .navigation-list li a {
    font-size: 30px;
  }

  .header-logo__desc {
    display: none;
  }

  .header-top__content {
    position: relative;
  }

  .header-top__content .header-contacts {
    padding-right: 70px;
  }

  .header-top__content .header-contacts__number {
    display: none;
  }

  .header__menu-btn {
    position: absolute;
    top: 8px;
    right: 20px;
    height: 25px;
    z-index: 30;
    display: block;
    width: 35px;
    cursor: pointer;
  }

  .header__menu-btn span {
    position: absolute;
    top: 10px;
    display: block;
    width: 100%;
    height: 3px;
    background-color: #ff6666;
    transition: all 0.3s ease-out;
  }

  .header__menu-btn span:first-child {
    top: 0;
  }

  .header__menu-btn span:last-child {
    top: 20px;
  }

  .header__menu-btn.open span:first-child {
    top: 10px;
    transform: rotate(45deg);
  }

  .header__menu-btn.open span:nth-child(2) {
    opacity: 0;
  }

  .header__menu-btn.open span:last-child {
    top: 10px;
    transform: rotate(-45deg);
  }

  .main-slider {
    margin-left: 25px;
  }

  .main-slider h1,
  .main-slider h2 {
    font-size: 50px;
    letter-spacing: inherit;
  }

  .main-slider h3 {
    font-size: 28px;
  }

  .main-slider h3::after {
    left: 5px;
  }

  .main-slider__price {
    padding: 5px 15px;
  }
}

@media (max-width: 767px) {
  .slick-arrow.slick-next {
    right: 40px;
    left: auto;
  }

  .slick-dots {
    left: 47%;
  }

  .slick-dots li {
    width: 14px;
    height: 14px;
  }
}

@media screen and (max-width: 767px) {
  .main-slider h1,
  .main-slider h2 {
